#ticker-image {
  -webkit-transform: scaleY(-1);
  transform: scaleY(-1);
  position: fixed;
  width: 50px;
  bottom: 960;
  fill: gold;
  background-color: gold;
}

#acquisitions {
  position: fixed;
}

div, .ticker-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

#button-textarea-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: right;
      -ms-flex-pack: right;
          justify-content: right;
}

#button-wrapper {
  display: block;
  right: 100px;
}

